//See https://github.com/casesandberg/react-reactions
const icons = {
  facebook: {
    like: 'data:image/png;base64,',
    love: 'data:image/png;base64,',
    haha: 'data:image/png;base64,',
    wow: 'data:image/png;base64,',
    sad: 'data:image/png;base64,',
    angry: 'data:image/png;base64,',
  },
}

export default {
  find: (scope, name) => {
    return icons[scope] && icons[scope][name] || 'n/a'
  },
}
